body {
  padding: 0;
  margin: 0 1em;
  color: hsl(0, 0%, 20%);
  background-color: hsl(0, 0%, 95%);
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, 
    Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue ,sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* highlight in house colours */
::-moz-selection,
::-moz-selection {
  color: hsl(0, 0%, 95%);
  background: hsl(11, 85%, 60%);
}
::selection,
::-moz-selection {
  color: hsl(0, 0%, 95%);
  background: hsl(11, 85%, 60%);
}
h1, h2 {
  margin: 0.2em 0;
}

h1, h2,
a {
  color: hsl(11, 85%, 60%);
}

a:hover {
  color: hsl(11, 100%, 50%);
  text-shadow: hsl(11, 85%, 60%) 0px 0px 35px;
}

header {
  text-align: center;
}

header .glyphs {
  display: grid;
  margin: 2em 0;
  grid-template-columns: repeat(3, auto);
}

header .glyphs .print-only-view,
.read-more .print-only-view {
  display: none;
  color: hsl(11, 85%, 60%);
}

.main-content {
  display: grid;
  grid-gap: 2em;
  grid-template-columns: -webkit-max-content auto;
  grid-template-columns: max-content auto;
}

main {
  display: inline-block;
}

main section {
  margin: 1em;
}

main section .headings {
  display: grid;
  grid-gap: 0.25em;
  grid-template-rows: repeat(2, -webkit-max-content);
  grid-template-rows: repeat(2, max-content);
  grid-template-columns: 1fr repeat(2, -webkit-max-content);
  grid-template-columns: 1fr repeat(2, max-content);
}

main section .headings h3,
main section .headings h4 {
  margin: 0;
}

main section .headings :nth-child(2) {
  grid-row: 2;
  grid-column: 1;
}

main section .headings :nth-child(3) {
  grid-row: 1/3;
  grid-column: 2;
  align-self: center;
} 

main section .headings :nth-child(4),
main section .headings :nth-child(5) {
  text-align: center;
}

main section .read-more {
  text-align: right;
}

aside {
  text-align: center;
  display: inline-block;
}

aside h2 {
  text-align: center;
}

aside .location {
  margin: 1em 0;
}

aside .skill-wall {
  display: grid;
  grid-gap: 2em;
  grid-template-columns: 16em;
  grid-template-rows: -webkit-max-content;
  grid-template-rows: max-content;
}

aside .skill-wall ul.skills-list {
  margin: 0;
  padding: 0;
  display: inline-block;
  grid-column-gap: 1em;
  column-gap: 1em;
  -moz-column-gap: 1em;
  -webkit-column-gap: 1em; 
  column-width: auto;
  -moz-column-width: auto;
  -webkit-column-width: auto;
   
 }
 
aside .skill-wall ul.skills-list li {
  margin: 0.1em;
  padding: 0.2em;
  border: 1px solid black;
  display: inline-block;
 }

 aside .skill-wall ul.skills-list li:hover {
  cursor: pointer;
  color: hsl(11, 85%, 60%);
 }

 aside .skill-wall .skill-description {
  padding: 1em;
}

aside .skill-wall .skill-description h3 {
  margin: 0 auto;
  color: hsl(11, 85%, 60%);
}

/* icons */
.icon-primary {
  fill: hsl(11, 85%, 60%);
}

.icon-secondary {
  fill: hsl(0, 0%, 20%);
}

.icon {
  width: 24px;
  height: auto;
  vertical-align: sub;
}

.icon.btn {
  padding: 8px;
}


/* print preview styles */
@media print {
  .glyphs a,
  .read-more a,
  aside .skill-wall .skill-description {
    display: none;
  }

  header .glyphs .print-only-view,
  .read-more .print-only-view {
    display: inline-block;
  }
}
